.App{
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    font-size: 25px;
    color: white;
}
.calc-box{
    background-color: black;
    display: inline-grid;
    padding: 5px;
    min-width: 400px;
    min-height: 400px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr;
    transition: 0.3;
}
.tab{color: white; text-align: right; grid-column: 1/5;align-self: self-end;}
.big-column{grid-column: 1/3;}
.tab-clear{background-color: red;transition: 0.3;color: white;}
.tab-relust{background: rgb(0, 68, 102);color: white;transition: 0.3; border: solid 0.5px black;}
.big-row{ grid-row: 5/7; grid-column: 4/4;}
.tab-number{background-color: #4d4d4d;color: white;transition: 0.3;}
.tab-option{background-color: rgb(102, 102, 102);color: white;transition: 0.3;}
.tab-clear:hover,.tab-relust:hover,.tab-number:hover,.tab-option:hover{
    color: wheat;
    border: solid 1px white;
    transition: 0.3;
}